/**
 * éè¯¯å¤çï¼ä¾å¦404
 * @param {Response} res - æå¡å¨ååº
 * @return {(Response|Promise)}
 * @private
 */
const handleError = (res) => {
  const { ok, statusText } = res;

  return ok ? res : Promise.reject(new Error(statusText));
};

/**
 * æ°æ®ç±»åè¿æ»¤ï¼ä»æ¥æ¶JSON
 * @param {Response} res - æå¡å¨ååº
 * @return {(Response|Promise)}
 * @private
 */
const handleContent = (res) => {
  const contentType = res.headers.get('content-type');

  if (/application\/json/i.test(contentType)) {
    return res.json();
  }

  return Promise.reject(new Error('Not a JSON'));
};

/**
 * å¢
 * @param {string} input - è¯·æ±URL
 * @param {Object} init - é¢å¤åæ°
 * @param {Object} init.headers
 * @param {(FormData|JSON)} init.body
 * @param {string} init.mode
 * @return {Promise}
 */
const post = (input, init = {}) => {
  const {
    headers,
    body = '',
    mode = 'no-cors',
  } = init;

  let data = '';
  let mime = '';
  if (body instanceof FormData) {
    data = body;
    mime = 'multipart/form-data';
  } else {
    data = JSON.stringify(body);
    mime = 'application/json';
  }

  const h = new Headers(headers);
  h.set('Content-Type', mime);
  h.set('Accept', 'application/json');

  return fetch(input, {
    method: 'POST',
    headers: h,
    body: data,
    mode,
  })
    .then(handleError)
    .then(handleContent);
};

/**
 * æ¥
 * @param {string} input - è¯·æ±URL
 * @param {Object} init - é¢å¤åæ°
 * @param {Object} init.headers
 * @param {string} init.mode
 * @return {Promise}
 */
const get = (input, init = {}) => {
  const { headers, mode = 'no-cors' } = init;

  const h = new Headers(headers);
  h.set('Accept', 'application/json');

  return fetch(input, {
    method: 'GET',
    headers: h,
    mode,
  })
    .then(handleError)
    .then(handleContent);
};

